* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background-color: #000;
  font-family: Poppins, sans-serif;
  font-size: 62.5%;
}

.container {
  text-align: center;
  margin-top: 50px;
  font-size: 30px;
}

.title-h1 {
  color: #fff;
  margin-bottom: 3rem;
}

.btn {
  cursor: pointer;
  color: #fff;
  background: #000;
  border: 1px solid #fff;
  border-radius: 50px;
  outline: none;
  width: 150px;
  height: 60px;
  margin-top: 25px;
  margin-bottom: 50px;
  font-size: 16px;
}

.box {
  cursor: pointer;
  background: #0e616e;
  border: 4px solid #000;
  border-radius: 12px;
  width: 180px;
  height: 180px;
  display: flex;
}

.board {
  justify-content: center;
  align-items: center;
  width: 564px;
  height: 600px;
  margin: auto;
  display: flex;
}

.box img {
  margin: 50px;
}

.title-h1 img {
  height: 90px;
  padding: 20px;
  position: relative;
  top: 24px;
}

@media (width <= 1200px) {
  .container {
    margin-top: 250px;
  }
}

@media (width <= 990px) {
  .container {
    margin-top: 200px;
  }
}

@media (width <= 768px) {
  .container {
    margin-top: 130px;
  }
}

@media screen and (width <= 450px) {
  .container {
    margin-top: 150px;
  }

  .box {
    width: 100px;
    height: 100px;
  }

  .board {
    width: 300px;
    height: 300px;
  }

  .btn {
    width: 100px;
    height: 50px;
  }

  .title-h1 {
    font-size: 25px;
  }
}
